body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, p, blockquote, th, td {
    margin: 0;
    padding: 0;
}

body {
    font-size: 12px;
    color: #666;
    font-family: Verdana, Microsoft YaHei, Simsun;
    background: #fff;
}

fieldset, img {
    border: 0;
}

ol, ul {
    list-style: none;
}

h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
}

em {
    font-style: normal;
}

input, button, select, textarea {
    outline: none;
}

textarea {
    resize: none;
}

p {
    text-align:justify;
    text-justify:distribute;
}

a {
    color: #666;
    text-decoration:none;
}

a:hover {
    color: #f60;
    text-decoration:none;
}

